.dialog_section {
  padding: 20px 20px 0 20px;
  background-color: #242426;
  border-radius: 10px;
}

.title_wrapper {
  margin-bottom: 20px;
}

.message_input {
  display: flex;
  align-items: center;
  margin: 30px 0 30px ;
}

.message_input > button {
  margin-left: 10px;
  height: 25px;
  width: 70px;
  background-color: #FDBA2B;
  border: none;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

textarea {
  resize: none;
  height: 40px;
  padding: 5px 5px 5px 10px;
  border-radius: 10px;
  width: 100%;
  background-color: rgba(49, 49, 49, 0.59);
  color: white;
  border: 1px solid rgba(183, 229, 209, 0.05);
}